import ActivityTypeSelector from '@/components/Activity/ActivityTypeSelector';
import BannerItem from '@/components/BannerItem';
import CommonCardFrame from '@/components/CommonCardFrame';
import LocationForm from '@/components/Forms/Location/index';
export default {
  components: {
    CommonCardFrame: CommonCardFrame,
    ActivityTypeSelector: ActivityTypeSelector,
    BannerItem: BannerItem,
    LocationForm: LocationForm
  },
  data: function data() {
    return {
      form: this.$form.createForm(this, {
        name: 'publishForm'
      }),
      bannerConfig: {
        media: {
          url: undefined,
          id: null
        },
        fit: 'cover'
      },
      maxCapacity: undefined,
      hasSelectedLocation: false,
      loading: false,
      pickerOptions: [],
      selectedLocation: {},
      freeTimeModal: false
    };
  },
  methods: {
    handleSubmitClick: function handleSubmitClick() {
      var _this = this;

      this.form.validateFields(function (err, values) {
        if (!err) {
          var locationParams = {};

          _this.$refs.locationForm.collectForm(function (params) {
            locationParams = params;
          });

          var params = Object.assign(values, locationParams);
          console.log(params);

          _this.$emit('submit', params);
        }
      });
    },
    handleBannerSelectClick: function handleBannerSelectClick(media) {
      console.log(media);
    },
    handleLocationSelect: function handleLocationSelect(location) {
      this.hasSelectedLocation = true;
      this.maxCapacity = location.max_capacity;
    }
  }
};