import { addActivityApplication } from '@/api/activityApplication';
import Form from "./components/Form";
import Content from "./components/Content";
export default {
  components: {
    Form: Form,
    Content: Content
  },
  data: function data() {
    return {
      content: ''
    };
  },
  methods: {
    handleSubmitClick: function handleSubmitClick(values) {
      var _this = this;

      this.isContentEmpty();
      values.content = this.content;
      this.$refs.form.loading = true;
      addActivityApplication(values).then(function (res) {
        if (res.status) {
          _this.$message.success('已提交申请');
        } else {
          _this.$message.error(res.message);
        }

        _this.$refs.form.loading = false;
      });
    },
    handleContentInput: function handleContentInput(content) {
      this.content = content;
    },
    isContentEmpty: function isContentEmpty() {
      if (this.content === '') {
        this.$message.error('活动内容不能为空');
      }
    }
  }
};